import Preloader from '../../common/preloader/preloader';
import s from './ProfileInfo.module.css'
import ProfileStatus from './ProfileStatus'
import userPhoto from './../../../assets/images/user.jpg'
import Profile from "../Profile";
import {useState} from "react";
import ProfileDataForm from "./ProfileDataForm";

const ProfileInfo = (props) => {
    let [editMode, setEditMode] = useState(false)
    if (!props.profile) {
        return <Preloader/>
    }
    const onMainPhotoSelected = (e) => {
        if (e.target.files.length) {
            props.savePhoto(e.target.files[0])
        }
    }
    const onSubmit = (formData) => {
        props.saveProfile(formData)
    }


    return <div>

        <div className={s.description}>
            <img src={props.profile.photos.large || userPhoto} className={s.mainPhoto}/>
            {
                props.isOwner ? <div><input type={'file'} onChange={onMainPhotoSelected}/></div> : <div></div>
            }

            {editMode
                ? <ProfileDataForm {...props} onSubmit={onSubmit} goToNotEditMode={() => {
                    setEditMode(false)}} />
                : <ProfileData {...props} goToEditMode={() => {
                    setEditMode(true)
                }}/>}


            <ProfileStatus status={props.status} updateStatus={props.updateStatus}/>
        </div>

    </div>
}

const Contact = ({contactTitle, contactValue}) => {
    return <div>
        <b>{contactTitle}</b>: {contactValue}
    </div>
}


const ProfileData = (props) => {
    return <div>
        {props.isOwner && <button onClick={() => props.goToEditMode()}>Редактировать</button>}
        <div>
            <b>Full Name</b>: {props.profile.fullName}
        </div>

        <div>
            <b>Lookin' for a job</b>: {props.profile.lookingForAJob ? 'Yes' : 'No'}
        </div>

        {props.profile.lookingForAJob &&
            <div><b>My professional skills</b>: {props.profile.lookingForAJobDescription} </div>}

        <div>
            <b>About me</b>: {props.profile.aboutMe}
        </div>

        <div>
            <b>Contacts</b>: {Object.keys(props.profile.contacts).map(key =>
            <Contact key={key} contactTitle={key} contactValue={props.profile.contacts[key]}/>
        )}
        </div>


    </div>
}


export default ProfileInfo;