import {Formik} from "formik";
import * as yup from "yup";
import React from "react";


const ProfileDataForm = (props) => {
    const validateScheme = yup.object().shape({
        fullName: yup.string().typeError('Должно быть строкой').min(1, 'Напиши хоть что то').max(100, 'Максимум 10 символов'),
        aboutMe: yup.string().typeError('Должно быть строкой').min(1, 'Напиши хоть что то').max(100, 'Максимум 100 символов'),
        lookingForAJobDescription: yup.string().typeError('Должно быть строкой').min(1, 'Напиши хоть что то').max(100, 'Максимум 100 символов'),

    })

    const onFullNameChange = (values) => {
      props.onSubmit(values);
      props.goToNotEditMode()
    }


    return  <Formik
            initialValues={
                {
                    fullName: props.profile.fullName,
                    aboutMe: props.profile.aboutMe,
                    lookingForAJob: props.profile.lookingForAJob,
                    lookingForAJobDescription: props.profile.lookingForAJobDescription

                }}
            validateOnChange
            onSubmit={(values) => {onFullNameChange(values)}}
            validationSchema={validateScheme}
        >
            {
                ({
                     values,
                     errors,
                     touched,
                     handleChange,
                     handleBlur,
                     isValid,
                     handleSubmit,
                     dirty
                 }
                ) => {
                    return <div>
                        <div>
                            <button
                            onClick={handleSubmit}
                            type={'submit'}>
                            Сохранить
                        </button></div>
                        <div>
                            <b>Full Name</b>:
                            <input
                                type={'text'}
                                name={'fullName'}
                                placeholder={values.fullName}
                                onBlur={handleBlur}
                                onChange={handleChange}
                                value={values.fullName}
                            />
                            {touched.fullName && errors.fullName && <p>{errors.fullName}</p>}
                        </div>

                        <div>
                            <b>Lookin' for a job</b>:
                            <input
                                type={'checkbox'}
                                name={'lookingForAJob'}
                                value={values.lookingForAJob}
                                onChange={handleChange}
                                checked={values.lookingForAJob ? true : false}/>
                            <label htmlFor="lookingForAJob">Yes</label>
                        </div>
                        {values.lookingForAJob && <div><b>My professional skills</b>:
                            <input
                                type={'text'}
                                name={'lookingForAJobDescription'}
                                placeholder={values.lookingForAJobDescription}
                                onBlur={handleBlur}
                                onChange={handleChange}
                                value={values.lookingForAJobDescription}
                            />
                            {touched.fullName && errors.fullName && <p>{errors.fullName}</p>}
                        </div>}


                        <div>
                            <b>About me</b>:
                            <input
                                type={'text'}
                                name={'aboutMe'}
                                placeholder={values.aboutMe}
                                onBlur={handleBlur}
                                onChange={handleChange}
                                value={values.aboutMe}
                            />
                            {touched.aboutMe && errors.aboutMe && <p>{errors.aboutMe}</p>}
                        </div>
                    </div>
                }
            }
        </Formik>

}


export default ProfileDataForm